import React, { useState } from 'react'



export default function Textform(props) {
    const handleUpClick = () => {
        //console.log("upperCase was click:" + text);
        let newText = text.toUpperCase();
        setText(newText);
        props.showalert(": Conveted to Upper Case!!", "success" )
    }
    const handleloClick = () => {
        
        let newText = text.toLowerCase();
        setText(newText);
        props.showalert(": Conveted to Lower Case!!", "success" )
    }
    const handlecleartext = () => {
    
        let newText =  ' ';
       
        setText(newText);
        props.showalert(": Your Text Clear  !!", "success" )
    }
    const handlenewClick = () => {
        
        let newText = text.replace(/(^\w{1})|(\s+\w{1})/g, letter => letter.toUpperCase());
      
        setText(newText);
        props.showalert(": Word Start with Capitalized Case  !!", "success" )
    }

    const handlereverseClick = () => {
        
        let newText = text.split("").reverse().join("");
      
        setText(newText);
        props.showalert(": Your Text Converted into Reverse Case !!", "success")
    }
    const handleCopyClick = () => {
       
        navigator.clipboard.writeText(text)
       // document.getSelection().removeAllRanges();
       
        props.showalert(": You copied Text", "success" )
    }
    const handleremoveExtraSpace = () => {
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "))
        props.showalert(": Your Extra Space Remove", "success" )
    }
    

    
    const handleOnChange = (eve) => {
        //console.log("Handle on change was click");
        setText(eve.target.value);
    }

    
    const [text, setText] = useState("");
    //text("changes text area");//wrong way to change set area
    //setText("changes text area11"); // correct way to change set area 
    return (
        <>
            <div className="container1" style={{color: props.mode ==='dark'?'white':'#042743'}}>
                <h3 className='mb-5'>{props.heading}</h3>
                <div className="mb-3">

                    <textarea className="form-control" placeholder='Enter your text' id="myBox" onChange={handleOnChange} style={{backgroundColor: props.mode ==='dark'?'#042743':'white', color: props.mode ==='dark'?'white':'#042743' }}value={text} rows="10"></textarea><br />
                    <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handleUpClick}>UPPER CASE</button> 
                    <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handleloClick}>lower case</button>
                    <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handlenewClick}>Capitalized Case</button>
                    <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handlereverseClick}>Reverse Case </button>
                    <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handleremoveExtraSpace}>Remove Extra Space </button>
                    <button disabled={text.length===0} className="btn btn-primary mx-1 my-1" onClick={handleCopyClick}>Copy Text  </button>
                    
                    <button  disabled={text.length===0} className="btn btn-primary mx-1" onClick={handlecleartext}>Clear Text</button>
                    
                </div>
            </div>

            <div className="container my-4 border border-secondary p-2 mb-2" style={{color: props.mode ==='dark'?'white':'#042743'}}>
                <h3>Your Text Summary </h3>
                <p><h6>Your Text is {text.split(/\s+/).filter((element)=>{return element.length!==0}).length} words and {text.length} characters</h6> </p>
                <p> <h6>Reads to Your text it takes {0.008 * text.split(" ").filter((element)=>{return element.length!==0}).length} minutes  </h6></p>
            </div>
            <div className="container my-4 border border-secondary p-2 mb-2" style={{color: props.mode ==='dark'?'white':'#042743'}}>
                <h3>Your Text Preview </h3>
                <hr></hr>

                <p>{text.length>0?text: "Enter Something in the textbox to Preview it "}</p>

            </div>


        </>
    )
}
