import React from 'react'


export default function About(props) {
   // const [myStyle, setMyStyle] = useState({
     //   color :'black',
       // backgroundColor: 'white'
   // })
    let myStyle = {

        color :props.mode === 'dark' ? 'white':'#042743',
        backgroundColor: props.mode ==='dark'? 'rgb(36 74 104)':'white',
     //   border: '1px solid',
       // borderColor: props.mode ===  'dark' ? 'white':'#042743'

    }


    return (
        <div className="container" >
            

            <div className="accordion" id="accordionExample">
            <h2 className="my-4" style={{color :props.mode === 'dark' ? 'white':'#042743'}}> About us</h2>
                <div className="accordion-item"  style={myStyle}>
                    <h2 className="accordion-header" id="headingOne">
                        <button className="accordion-button" type="button"  style={myStyle} data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                       <strong> What is Flame-TextUtils </strong>
                        </button>
                    </h2>
                    <div id="collapseOne" className="accordion-collapse collapse show" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
                        <div className="accordion-body" style={myStyle}>
                        <ul>
                        <li>    Flame-TextUtils is a web tool to assist developers and other people in daily tasks by providing tools for manipulating text data.
                            </li> 
                            <li>
                            Flame-TextUtils provides tools for formatting source code, converters, tools for handling text, such as remove duplicate characters, empty lines, text sorter and many others. 
                                </li> 
                            
                            
                            
                            </ul>


                            
                            
                            
                            </div>
                    </div>
                </div>
                <div className="accordion-item" style={myStyle}>
                    <h2 className="accordion-header" id="headingTwo">
                        <button className="accordion-button collapsed" type="button"   style={myStyle} data-bs-toggle="collapse" data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                          <strong>  Why I use Flame-TextUtils Tools instead of Other Textutils ?</strong>
                        </button>
                    </h2>
                    <div id="collapseTwo" className="accordion-collapse collapse"  aria-labelledby="headingTwo" style={myStyle} data-bs-parent="#accordionExample">
                        <div className="accordion-body" style={myStyle}>
                        <ul>
                            <li>Flame-TextUtils is Build by React.js framework is an open-source JavaScript framework and library developed by Facebook. 
                                </li>
                                <li>Its used for building interactive user interfaces and web applications quickly and efficiently with significantly less code.
                                    </li>
                                    <li> Flame-textutils works without your page reload .  </li>
                        

                        </ul>
                           
                           
                            </div>
                    </div>
                </div>
                <div className="accordion-item"  style={myStyle}>
                    <h2 className="accordion-header" id="headingThree"  style={myStyle}>
                        <button className="accordion-button collapsed"  style={myStyle} type="button" data-bs-toggle="collapse" data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                           <strong> Flame-TextUtils Features</strong>
                        </button>
                    </h2>
                    <div id="collapseThree" className="accordion-collapse collapse" style={myStyle}  aria-labelledby="headingThree" data-bs-parent="#accordionExample">
                        <div className="accordion-body"  style={myStyle}>
                           <ul>
                            <li>LowerCase To UpperCase</li>
                            <li>UpperCase To LowerCase</li>
                            <li>Copied Text</li>
                            <li>Capitalized Case</li>
                            <li>Reverse Case </li>
                            <li>Remove Extra Spaces </li>
                            <li>Clear Text</li>
                            <li>Count your Words </li>
                            <li>Count Your Characters</li>
                            <li>Preview Your Text</li>




                           </ul>
                           
                           
                           
                            </div>
                    </div>
                </div>
            </div>
            
        </div>
    )
}
