import './App.css';
import React, { useState } from 'react'



import About from './Components/About';

import Navbar from './Components/Navbar';
import Textform from './Components/Textform';
import Alert from './Components/Alert';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  
} from "react-router-dom";

import Footer from './Components/Footer';







function App() {
  const [alert, setAlert] = useState(null);
  const showalert = (message, type) => {

    setAlert({
      msg: message,
      type: type
    })
    setTimeout(() => {
      setAlert(null);
    }, 1400);
  }

  const [mode, setMode] = useState('light');
  const changeMode = () => {
    if (mode === 'light') {
      setMode('dark')
      document.body.style.backgroundColor = '#042743'
      showalert(":Dark mode has been enabled", "success");
      //document.title = "TextUtils - Dark Mode";
      /*setInterval(() => {
      document.title = 'TextUtils is best'
      }, 2000);
      setInterval(() => {
        document.title = 'TextUtils is Perfect'
      }, 1500);*/
    }
    else {
      setMode('light')
      document.body.style.backgroundColor = 'white'
      showalert(":Light mode has been enabled", "success")
      //document.title = "TextUtils - Light Mode";
    }

  }
  return (

    <>
    <Router>
      <Navbar title="Flame-TextUtils " mode={mode} toggleMode={changeMode} about="About Us"  />
      <Alert alert={alert} />
      <div className="container my-4">
        <Routes>
          <Route exact path="/about" element={<About mode={mode} />} >
            
          </Route>  
          <Route exact path="/" element={<Textform showalert={showalert} heading="Flame-TextUtils -  Word Counter, Character Counter, Remove Extra Spaces, LowerCase to UpperCase" mode={mode} />
          }>
          </Route>
         
          

        </Routes>


      </div>
      </Router>
          <Footer/>

    </>
  
  );
}

export default App;
